.buy-body {
  width: 100%;
  height: 500px;
  margin-top: 50px;
}

.bg-image {
  border: black;
  /* The image used */
  /* Add the blur effect */
  filter: blur(5px);
  -webkit-filter: blur(5px);

  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../assets/chickens.jpg") no-repeat center;
  background-size: cover;
}

.bg-text {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 100%;
  padding: 20px;
  text-align: center;
}

.bg-text .description {
  color: white;
  font-weight: lighter;
  font-size: 25px;
}

.phone-number-button {
  height: 100%;
  width: 35%;
  font-weight: lighter;
  font-size: 20px;
  color: white;
  background-color: red;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.phone-number-button:hover {
  text-decoration: none;
}

.phone-number-button:active {
  color: white;
}

.button-container {
  padding-top: 20px;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-container {
  padding-top: 20px;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-img {
  margin-left: 20px;
  width: 50px;
  height: 50px;
}
